@tailwind base;
@tailwind components;
@tailwind utilities;
:root {
    --background: #ef233b9a;
}

body {
    background-color: #2b2d42;
}


/* 
Color Palatte:

Body = #2b2d42
Text = #edf2f4
Accent = #ef233c

*/

.nav-item .active {
    color: rgb(248 113 113);
    transition: all 0.5s ease;
}

ScrollLink {
    cursor: pointer;
}

.container {
    align-items: center;
    display: flex;
    justify-content: center;
}

.shape {
    background: linear-gradient(45deg, var(--background) 0%, var(--background) 100%);
    animation: morph 8s ease-in-out infinite;
    border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%;
    height: 300px;
    transition: all 1s ease-in-out;
    width: 300px;
    z-index: 5;
}

@keyframes morph {
    0% {
        border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%;
        background: linear-gradient(45deg, var(--background) 0%, var(--background) 100%);
    }
    50% {
        border-radius: 30% 60% 70% 40% / 50% 60% 30% 60%;
        background: linear-gradient(45deg, var(--background) 0%, var(--background) 100%);
    }
    100% {
        border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%;
        background: linear-gradient(45deg, var(--background) 0%, var(--background) 100%);
    }
}

.context {
    width: 100%;
    position: absolute;
    top: 50vh;
}

.context h1 {
    text-align: center;
    color: #fff;
    font-size: 50px;
}

.circles {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -100;
    overflow: hidden;
}

.circles .square-up {
    position: absolute;
    display: block;
    list-style: none;
    width: 20px;
    height: 20px;
    background: rgba(255, 255, 255, 0.119);
    animation: animate 25s linear infinite;
    bottom: -150px;
}

.circles .square-up:nth-child(1) {
    left: 25%;
    width: 80px;
    height: 80px;
    animation-delay: 0s;
    animation-duration: 15s;
}

.circles .square-up:nth-child(2) {
    left: 10%;
    width: 20px;
    height: 20px;
    animation-delay: 2s;
    animation-duration: 12s;
}

.circles .square-up:nth-child(3) {
    left: 70%;
    width: 20px;
    height: 20px;
    animation-delay: 6s;
}

.circles .square-up:nth-child(4) {
    left: 40%;
    width: 60px;
    height: 60px;
    animation-delay: 0s;
    animation-duration: 18s;
}

.circles .square-up:nth-child(5) {
    left: 65%;
    width: 20px;
    height: 20px;
    animation-delay: 0s;
    animation-duration: 10s;
}

.circles .square-up:nth-child(6) {
    left: 75%;
    width: 110px;
    height: 110px;
    animation-delay: 3s;
    animation-duration: 15s;
}

.circles .square-up:nth-child(7) {
    left: 35%;
    width: 130px;
    height: 130px;
    animation-delay: 6s;
}

.circles .square-up:nth-child(8) {
    left: 50%;
    width: 25px;
    height: 25px;
    animation-delay: 15s;
    animation-duration: 20s;
}

.circles .square-up:nth-child(9) {
    left: 20%;
    width: 15px;
    height: 15px;
    animation-delay: 2s;
    animation-duration: 25s;
}

.circles .square-up:nth-child(10) {
    left: 85%;
    width: 150px;
    height: 150px;
    animation-delay: 0s;
    animation-duration: 11s;
}

.circles .square-up:nth-child(11) {
    left: 7%;
    width: 30px;
    height: 30px;
    animation-delay: 0s;
    animation-duration: 6s;
}

.circles .square-up:nth-child(12) {
    left: 66%;
    width: 75px;
    height: 75px;
    animation-delay: 0s;
    animation-duration: 8s;
}

.circles .square-up:nth-child(13) {
    left: 17%;
    width: 100px;
    height: 100px;
    animation-delay: 0s;
    animation-duration: 10s;
}

@keyframes animate {
    0% {
        transform: translateY(0) rotate(0deg);
        opacity: 1;
        border-radius: 0;
    }
    100% {
        transform: translateY(-1000px) rotate(720deg);
        opacity: 0;
        border-radius: 50%;
    }
}